import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
import { UsersService } from '../../services/users.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  registerFormGroup: FormGroup

  constructor(
    private fb: FormBuilder, 
    private _user: UsersService,
    private _flash: FlashMessagesService,
    private _router: Router
  ) { 
    this.createForm()
  }

  ngOnInit() {
  }

  createForm() {
    this.registerFormGroup = this.fb.group({
      name:['', [Validators.required, Validators.minLength(5), Validators.maxLength(50)]],
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(50)]],
      isAdmin: false
    })
  }

  register(userInfo) {
    userInfo.isAdmin = typeof(userInfo.isAdmin) !== 'boolean' && userInfo.isAdmin == "true" ? true : false
    
    this._user.register(userInfo).subscribe(
      res => {
        if(res.hasOwnProperty('_id')) {
          this._flash.show('Registered Successfully', { cssClass: 'alert alert-success' })
          this._router.navigate(['/login'])
        }
      },
      err => {
        console.log(err)
        this._flash.show(err._body, { cssClass: 'alert alert-warning' })
      }
    )
  }

}
