import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MoviesService } from '../../services/movies.service';
import { CustomersService } from '../../services/customers.service';
import { RentalsService } from '../../services/rentals.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-rent-movie',
  templateUrl: './rent-movie.component.html',
  styleUrls: ['./rent-movie.component.css']
})
export class RentMovieComponent implements OnInit {

  @ViewChild('searchBar') searchBar: ElementRef

  haveCustomer: boolean = true

  movieId: string
  movie: any

  customers: Array<any>

  customerFormGroup: FormGroup

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _movie: MoviesService,
    private _customer: CustomersService,
    private _rental: RentalsService,
    private _flash: FlashMessagesService,
    private _fb: FormBuilder
  ) {
    this.createForm()
  }

  ngOnInit() {
    this._activatedRoute.params.subscribe(
      params => {
        this.movieId = params.id
        this._movie.getSingle(this.movieId).subscribe(
          data => this.movie = data
        )
      }
    )

    this._customer.loadCustomers().subscribe(
      customers => this.customers = customers
    )
  }

  makeRental(movieId, customerId) {
    if (customerId == "default") {
      this._flash.show('Please choose a customer...', { cssClass: 'alert alert-warning' })
      return
    }
    this._rental.rent(movieId, customerId).subscribe(
      res => {
        if (res.hasOwnProperty('_id')) {
          this._flash.show('Rental was made successfully', { cssClass: 'alert alert-success' })
          this.movie.numberInStock--
        }
      },
      err => {
        this._flash.show(err._body, { cssClass: 'alert alert-warning' })
      }
    )
  }

  createForm() {
    this.customerFormGroup = this._fb.group({
      name: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(50)]],
      email: ['', [Validators.required, Validators.email, Validators.minLength(5), Validators.maxLength(50)]],
      phone: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(50)]]
    })
  }

  createCustomer(customerInfo: any) {
    this._customer.createCustomer(customerInfo).subscribe(
      data => {
        if (data.hasOwnProperty('_id')) {
          this._flash.show('Create successfully!', { cssClass: 'alert alert-success' })

          this._customer.loadCustomers().subscribe(
            customers => this.customers = customers
          )

          setTimeout(() => {
            this.haveCustomer = true
          }, 500);
        }
      },
      err => console.log(err)
    )
  }

}
