import { Component, OnInit } from '@angular/core';
import { UsersService } from '../../services/users.service';
import { DataTransfererService } from '../../services/data-transferer.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {

  token: string
  user: any

  constructor(
    private _user: UsersService,
    private _receiver: DataTransfererService,
    private _router: Router
  ) {
    this._receiver.$token.subscribe(
      data => {
        if (!data) return

        this.token = data
        this._user.loadMe(this.token).subscribe(
          res => this.user = res,
          err => console.log(err)
        )
      },
      err => console.log(err)
    )
  }

  ngOnInit() {
    this.token = localStorage.getItem('token')

    if (this.token == null) {
      this.user = null
      return
    }
    this._user.loadMe(this.token).subscribe(
      res => this.user = res,
      err => console.log(err)
    )
  }

  logout(){
    this.token = null
    this.user = null
    localStorage.removeItem('token')
    this._router.navigate(['/'])
  }
}
