import { Injectable } from '@angular/core';
import { Http, Response, Headers } from '@angular/http';
import { Observable } from 'rxjs';
import {map} from 'rxjs/operators'

@Injectable({
  providedIn: 'root'
})
export class RentalsService {

  constructor(private _http: Http) { }

  getRentals():Observable<any> {
    let rentalsApi = `api/rentals`
    
    return this._http.get(rentalsApi).pipe(
      map(res => res.json())
    )
  }

  rent(movieId, customerId):Observable<any> {
    let rentApi = `api/rentals`

    let token = localStorage.getItem('token')
    let rentHeader = new Headers()
    rentHeader.append('x-auth-token', token)

    const rental = { movieId, customerId }
    
    return this._http.post(rentApi, rental, { headers: rentHeader }).pipe(
      map(res => res.json())
    )
  }

  return(rental):Observable<any> {
    let returnApi = `api/returns`

    let token = localStorage.getItem('token')
    let returnHeader = new Headers()
    returnHeader.append('x-auth-token', token)

    return this._http.post(returnApi, rental, { headers: returnHeader }).pipe(
      map(res => res.json())
    )
  }
}
