import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthService } from '../../services/auth.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import { Router } from '@angular/router';
import { DataTransfererService } from '../../services/data-transferer.service';
import { GuardsService } from '../../services/guards.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  token: string
  loginFormGroup: FormGroup
  
  constructor(
    private fb: FormBuilder, 
    private _auth: AuthService,
    private _flash: FlashMessagesService,
    private _router: Router,
    private _transferer: DataTransfererService,
    private _guard: GuardsService
  ) {
    this.createForm()
  }

  ngOnInit() {
  }

  createForm() {
    this.loginFormGroup = this.fb.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(50)]]
    })
  }

  login(account) {
    this._auth.login(account.email, account.password).subscribe(
      res => {
        if(res.status == 200) {
          this.token = res._body
          localStorage.setItem('token', this.token)
          this._transferer.transferToken(this.token)
          this._flash.show('You are now logged in!', { cssClass: 'alert alert-success' });
          this._router.navigate(['/movies'])
        }       
      },
      err => {        
        let error = {
          status: err.status,
          message: err._body
        }
        this._flash.show(error.message, { cssClass: 'alert alert-danger' });
      }
    )
  }

}
