import { Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { map } from "rxjs/operators";
import { Http, Headers } from '@angular/http';

@Injectable({
  providedIn: 'root'
})
export class CustomersService {

  constructor(private _http: Http) { }

  loadCustomers():Observable<any> {
    let customersApi = `api/customers`

    return this._http.get(customersApi).pipe(
      map(res => res.json())
    )
  }

  createCustomer(customer):Observable<any> {
    let createApi = `api/customers`

    let token = localStorage.getItem('token')
    let createHeader = new Headers()
    createHeader.append('x-auth-token', token)

    return this._http.post(createApi, customer, { headers: createHeader }).pipe(
      map(res => res.json())
    )
  }
}
