import { Component, OnInit, ViewChild   } from '@angular/core';
import { RentalsService } from '../../services/rentals.service';
import { MatSort, MatTableDataSource } from '@angular/material';
import { FlashMessagesService } from 'angular2-flash-messages';
import { error } from '@angular/compiler/src/util';

@Component({
  selector: 'app-rentals',
  templateUrl: './rentals.component.html',
  styleUrls: ['./rentals.component.css']
})
export class RentalsComponent implements OnInit {

  
  @ViewChild(MatSort) sort: MatSort;
  
  rentals: Array<any>
  
  displayedColumns: string[] = ['customer.name', 'movie.title', 'dateOut', 'return'];
  dataSource: MatTableDataSource<any>
  constructor(private _rental: RentalsService, private _flash: FlashMessagesService) { }

  ngOnInit() {
    this.loadRentals()
  }

  loadRentals() {
    this._rental.getRentals().subscribe(
      rentals => {
        this.rentals = rentals
        this.renderTable(this.rentals)
      }
    )
  }

  renderTable(data) {
    this.dataSource = new MatTableDataSource(data)
    this.dataSource.sortingDataAccessor = (item, property) => {
      switch(property) {
        case 'customer.name': return item.customer.name;
        case 'movie.title': return item.movie.title;
        default: return item[property];
      }
    };
    this.dataSource.sort = this.sort
  }

  return(rental) {
    const rentalInfo = {
      movieId: rental.movie._id,
      customerId: rental.customer._id,
      rentalId: rental._id
    }
    
    this._rental.return(rentalInfo).subscribe(
      res => {
        if ( res.hasOwnProperty('_id')) {
          this._flash.show('Return successfully', { cssClass: 'alert alert-success'})
          const rentalInTbl = this.rentals.find(rental => rental._id === res._id)
          const index = this.rentals.indexOf(rentalInTbl)

          this.rentals.splice(index, 1, res)

          this.renderTable(this.rentals)
        }
      },
      err => this._flash.show(err._body, { cssClass: 'alert alert-danger'})
    )
  }

}
