import { Injectable } from '@angular/core';
import { Http, Headers, Response } from '@angular/http';
import { map } from "rxjs/operators";

@Injectable({
  providedIn: 'root'
})
export class UsersService {

  constructor(private _http: Http) { }

  loadMe(token) {
    let meApi = `api/users/me`
    let meHeader = new Headers()
    meHeader.append('x-auth-token', token)

    return this._http.get(meApi, {headers: meHeader}).pipe(
      map((res: Response) => res.json())
    )
  }

  register(newUser: any) {
    let registerApi = `api/users/`

    return this._http.post(registerApi, newUser).pipe(
      map((res: Response) => res.json())
    )
  }
}
