import { Injectable } from '@angular/core';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataTransfererService {

  transferingToken = new BehaviorSubject(null)

  $token = this.transferingToken.asObservable()

  constructor() { }

  transferToken(token) {
    this.transferingToken.next(token)
  }
}
